<template>
<div class="DetailsComments" style="padding: 0 40px;">
  <AddRankCard class="DetailsComments_addCarg" :vData="data.detail"/>
  <div class="DetailsComments_evaluate">
    {{data.detail.content}}
  </div>
  <PictureBar @getIndex="getImg" :vData="data.detail.imgs_url" /><!--监听子组件picture-bar.vue触发的imgClick事件,然后调用getImg方法-->
  <div class="text-center DetailsComments_loveBtn">
    <div class="PinkBtn loveBtn" @click="clickLink(data.detail.id)" v-if="data.detail.like_state">
      <span class="icon-in_like_icon"></span>
    </div>
    <div class="GrayBtn loveBtn" @click="clickLink(data.detail.id,true)" v-else>
      <span class="icon-in_like_icon"></span>
    </div>
  </div>
  <ul class="list_of_heads">
    <li class="headPortrait_li" v-for="(like,index) in data.likes" v-if="index<4" :key="like.id">
      <a href="javascript:;">
        <img :src="like.user_profile.profile.avatar_path" alt="" class="headPortrait_li_img" />
      </a>
    </li>
    <span class="headPortrait_total headPortrait_li">+{{likes}}</span>
  </ul>

  <div class="DetailsComments_reviewProduct">
    <h3 class="DetailsComments_title">点评的产品</h3>
    <ProductBar :vData="data.product" />
  </div>

  <div class="DetailsComments_relatedLabels" v-if="data.detail.tagslength>=1">
    <h3 class="DetailsComments_title">相关标签</h3>
    <TagBar :vData="data.detail.tags" />
  </div>

  <div class="DetailsComments_relatedComments" v-if="data.reviews.length>=1">
    <h3 class="DetailsComments_title">相关点评</h3>
		<div v-for="(review,index) in data.reviews" :key="review">
