	computed:{
		likes(){
			if(this.data.detail.likes>999){
				return 999
			}else{
				return this.data.detail.likes
			}
		}
	},
  methods: {
    getImg(_index) {
			let imgUrl = this.data.detail.imgs_url[_index]
			nativeBrowse(imgUrl)
			if(!isApp){}
		},
		clickLink(_review_id,_isDel) {//_isDel==取消点赞
			if(!_isDel){
				nativeCommentLink({review_id:_review_id})
			}else{
				nativeCommentLink({review_id:_review_id,del:_isDel})
			}
			this.data.detail.like_state = !this.data.detail.like_state
			if(!isApp){}
			
		},
		getLikeIndex({id,isCancel}){
			if(!isCancel){
				nativeCommentReviewLink({story_comment_id:id})
			}else{
				nativeCommentReviewLink({story_comment_id:id,del:isCancel})
			}
		}
  },
